.navboxes .stip {
    position: relative;
    right: 53.5vmin;
    top: -27px
}

.navboxes .btn-group {
    flex: 2;
    right: 0;
}

.navboxes .btn-group .btn-primary {
    background: #004aad;
    color: #fff;
    border-color: #6172f2;
    font-family: GlacialIndifferenceItalic;
    font-weight: 700
}

.navboxes .btn-group .btn-primary:hover {
    background: #0060e0;
    color: #fff;
    border-color: #004aad
}

.navboxes .btn-group .btn-light {
    background: #faf7f7;
    color: #000;
    border-color: #6172f2;
    font-family: GlacialIndifferenceItalic;
    font-weight: 700
}

.navboxes .btn-group .btn-light:hover {
    background: #fff;
    color: #000;
    border-color: #faf7f7
}

.navboxes .btn-group .btn-lg {
    width: 200px
}

.navboxes .btn-group body {
    background: #222
}

.navboxes .btn-group button {
    border-radius: 0;
    cursor: pointer
}

